import * as THREE from "three";
import GUI from "lil-gui";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";

// =============================================================================
// PHYSICS IMPLEMENTATION
// =============================================================================

class HeronPhysics {
  constructor() {
    // --- Costanti del Modello Fisico ---
    this.alpha = 1 / 1024;
    this.beta = 1 / 502;
    this.gamma = 1 / 502;

    // Costanti Fisiche
    this.rho = 1000.0;
    this.g = 9.8;
    this.p_atm = 101325.0;

    // Parametri Geometrici del Sistema (in metri quadrati o metri)
    this.S_B = 0.0142;
    this.S_C = 0.0142;
    this.V_D0 = 0.00355;

    // Livelli di Riferimento Iniziali
    this.h_B0_ref = 0.04;
    this.h_C0_ref = 0.21;

    // Altezze Verticali Fisse
    this.h_6 = 0.33;
    this.H = 0.25;

    // Perdite viscose (Poiseuille)
    this.eta = 0.001;
    this.S = 0.0000285;
    this.L1 = 0.22;
    this.L2 = 0.31;

    this.B1 = (8 * Math.PI * this.eta * this.L1) / this.S;
    this.B2 = (8 * Math.PI * this.eta * this.L2) / this.S;

    this.y0 = [0.06, 0.04, 0.2];

    this.EPS_DHB = 1e-4;
    this.T_GUARD = 10.0;

    this.t = 0.0;
    this.h_A = 0.0;
    this.h_B = 0.0;
    this.h_C = 0.0;
    this.v4 = 0.0;
    this.isStable = false;

    this.reset();
  }

  reset() {
    [this.h_A, this.h_B, this.h_C] = this.y0;
    this.t = 0.0;
    this.v4 = 0.0;
    this.isStable = false;
  }

  system(h_A, h_B, h_C) {
    const delta_V =
      (this.h_B0_ref - h_B) * this.S_B + (this.h_C0_ref - h_C) * this.S_C;
    const V_D = this.V_D0 + delta_V;
    const p_D = (this.p_atm * this.V_D0) / V_D;

    const deltaP_AB =
      this.p_atm - p_D + this.rho * this.g * (h_A + this.H - h_B);
    const deltaP_C4 = p_D - this.p_atm + this.rho * this.g * (h_C - this.h_6);

    const v2 =
      (-this.B1 +
        Math.sqrt(this.B1 ** 2 + 2 * this.rho * Math.max(deltaP_AB, 0.0))) /
      this.rho;
    const v4 =
      (-this.B2 +
        Math.sqrt(this.B2 ** 2 + 2 * this.rho * Math.max(deltaP_C4, 0.0))) /
      this.rho;

    this.v4 = v4;

    const dhA_dt = -this.alpha * v2 + this.alpha * v4;
    const dhB_dt = this.beta * v2;
    const dhC_dt = -this.gamma * v4;

    return [dhA_dt, dhB_dt, dhC_dt];
  }

  step(dt) {
    if (this.isStable) return;

    const [dhA_dt, dhB_dt, dhC_dt] = this.system(this.h_A, this.h_B, this.h_C);

    if (this.t > this.T_GUARD && Math.abs(dhB_dt) < this.EPS_DHB) {
      this.isStable = true;
      this.v4 = 0;
      return;
    }

    this.h_A += dhA_dt * dt;
    this.h_B += dhB_dt * dt;
    this.h_C += dhC_dt * dt;
    this.t += dt;

    this.h_A = Math.max(0, this.h_A);
    this.h_B = Math.max(0, this.h_B);
    this.h_C = Math.max(0, this.h_C);
  }
}

// =============================================================================
// THREE.JS SCENE IMPLEMENTATION
// =============================================================================

class HeronFountainScene {
  constructor(containerId = "scene-container") {
    this.container = document.getElementById(containerId);
    if (!this.container)
      throw new Error(`Container element with id '${containerId}' not found.`);

    this.guiParams = {
      isRunning: false,
      timeScale: 1.0,
      viewMode: "3D",
      playPause: () => {
        this.guiParams.isRunning = !this.guiParams.isRunning;
        this.handlePlayPause();
      },
      reset: () => {
        this.resetAll();
      },
    };

    // --- Setup di base ---
    this.clock = new THREE.Clock(false);
    this.elapsedTime = 0.0;
    this.scene = new THREE.Scene();
    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.controls = null;
    this.camera2D = null;
    this.camera3D = null;
    this.camera = null;
    this.h_jet = 0.0;

    // --- Oggetti di scena ---
    this.scale = 20; // 1 meter = 20 units in three.js
    this.physics = new HeronPhysics();
    this.waterA = null;
    this.waterB = null;
    this.waterC = null;
    this.fountainParticles = null;
    this.particleVelocities = null;
    this.particleCount = 2500; // moderate count for performance
    this.textOverlay = null;
    this.textGroups = {};

    // --- Costanti di layout (scalate) ---
    this.H_scaled = this.physics.H * this.scale;
    this.h6_scaled = this.physics.h_6 * this.scale;

    // Layout sizes
    this.worldWidth = 0.3 * this.scale;
    this.worldDepth = 0.1 * this.scale;
    this.internalWallBCH = 0.22 * this.scale;

    this.topBasinY = this.H_scaled;
    this.topBasinHeight = 6;

    this.initThree();
    this.setupSceneGeometry();
    this.setupGUI();
    this.addTextLabels();
    this.resetAll();

    this.animate = this.animate.bind(this);
    this.animate();
  }

  initThree() {
    this.scene.background = new THREE.Color(0x1a1a2a);

    this.renderer.setSize(
      this.container.clientWidth,
      this.container.clientHeight
    );
    this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
    this.renderer.shadowMap.enabled = true;
    this.container.appendChild(this.renderer.domElement);

    const aspect = this.container.clientWidth / this.container.clientHeight;
    const viewSize = 15;

    this.camera2D = new THREE.OrthographicCamera(
      -viewSize * aspect,
      viewSize * aspect,
      viewSize,
      -viewSize,
      0.1,
      100
    );
    this.camera2D.position.set(0, 0, 50);
    this.camera2D.lookAt(0, this.H_scaled, 0);

    this.camera3D = new THREE.PerspectiveCamera(50, aspect, 0.1, 200);
    this.camera3D.position.set(15, 12, 25);
    this.camera3D.lookAt(0, this.H_scaled, 0);

    this.camera = this.camera3D;

    this.controls = new OrbitControls(this.camera3D, this.renderer.domElement);
    this.controls.enableDamping = true;
    this.controls.dampingFactor = 0.05;
    this.controls.target.set(0, this.H_scaled, 0);
    this.controls.enabled = this.guiParams.viewMode === "3D";

    const ambientLight = new THREE.AmbientLight(0xffffff, 0.7);
    this.scene.add(ambientLight);

    const directionalLight = new THREE.DirectionalLight(0xffffff, 1.0);
    directionalLight.position.set(10, 20, 15);
    directionalLight.castShadow = true;
    this.scene.add(directionalLight);

    window.addEventListener("resize", this.onWindowResize.bind(this));
  }

  setupSceneGeometry() {
    const waterMaterial = new THREE.MeshPhysicalMaterial({
      color: 0x2080ff,
      transmission: 0.9,
      thickness: 1.5 * this.scale,
      roughness: 0.1,
      ior: 1.33,
      transparent: true,
      side: THREE.DoubleSide,
    });

    const glassMaterial = new THREE.MeshPhysicalMaterial({
      color: 0xffffff,
      transmission: 0.95,
      thickness: 0.5,
      roughness: 0.05,
      ior: 1.52,
      transparent: true,
      opacity: 0.3,
      side: THREE.DoubleSide,
    });

    const pipeMaterial = new THREE.MeshStandardMaterial({
      color: 0x909090,
      roughness: 0.3,
      metalness: 0.5,
    });

    const basinWidth = this.worldWidth / 2;
    const basinDepth = this.worldDepth;

    // Water boxes
    const waterGeom = new THREE.BoxGeometry(1, 1, 1);
    this.waterA = new THREE.Mesh(waterGeom, waterMaterial.clone());
    this.waterA.scale.set(this.worldWidth, 1, basinDepth);
    this.scene.add(this.waterA);

    this.waterB = new THREE.Mesh(waterGeom, waterMaterial.clone());
    this.waterB.scale.set(basinWidth, 1, basinDepth);
    this.waterB.position.x = -basinWidth / 2;
    this.scene.add(this.waterB);

    this.waterC = new THREE.Mesh(waterGeom, waterMaterial.clone());
    this.waterC.scale.set(basinWidth, 1, basinDepth);
    this.waterC.position.x = basinWidth / 2;
    this.scene.add(this.waterC);

    // Walls (glass)
    const wallThickness = 0.2;
    const createWall = (w, h, d, x, y, z) => {
      const geom = new THREE.BoxGeometry(w, h, d);
      const mesh = new THREE.Mesh(geom, glassMaterial);
      mesh.position.set(x, y, z);
      this.scene.add(mesh);
      return mesh;
    };

    createWall(
      this.worldWidth + wallThickness * 2,
      wallThickness,
      basinDepth + wallThickness * 2,
      0,
      -wallThickness / 2,
      0
    );
    createWall(
      this.worldWidth + wallThickness * 2,
      wallThickness,
      basinDepth + wallThickness * 2,
      0,
      this.H_scaled,
      0
    );
    createWall(
      this.worldWidth + wallThickness * 2,
      wallThickness,
      basinDepth + wallThickness * 2,
      0,
      this.topBasinY + this.topBasinHeight,
      0
    );

    const totalHeight = this.topBasinY + this.topBasinHeight;
    createWall(
      wallThickness,
      totalHeight,
      basinDepth + wallThickness,
      -this.worldWidth / 2 - wallThickness / 2,
      totalHeight / 2 - wallThickness / 2,
      0
    );
    createWall(
      wallThickness,
      totalHeight,
      basinDepth + wallThickness,
      this.worldWidth / 2 + wallThickness / 2,
      totalHeight / 2 - wallThickness / 2,
      0
    );
    createWall(
      this.worldWidth + wallThickness * 2,
      totalHeight,
      wallThickness,
      0,
      totalHeight / 2 - wallThickness / 2,
      -basinDepth / 2 - wallThickness / 2
    );
    createWall(
      this.worldWidth + wallThickness * 2,
      totalHeight,
      wallThickness,
      0,
      totalHeight / 2 - wallThickness / 2,
      basinDepth / 2 + wallThickness / 2
    );

    // Internal divider
    createWall(
      wallThickness,
      this.internalWallBCH,
      basinDepth,
      0,
      this.internalWallBCH / 2,
      0
    );

    // Pipes
    const pipeRadius = 0.2;
    const createPipe = (height, x, y, z) => {
      const geom = new THREE.CylinderGeometry(
        pipeRadius,
        pipeRadius,
        height,
        16
      );
      const mesh = new THREE.Mesh(geom, pipeMaterial);
      mesh.position.set(x, y, z);
      this.scene.add(mesh);
      return mesh;
    };

    // Tubo 1-4 (A -> B)
    createPipe(this.H_scaled, -basinWidth / 2, this.H_scaled / 2, 0);

    // Tubo 5-6 (C -> A -> Jet)
    // store reference to pipe so we can find nozzle
    this.pipe56 = createPipe(
      this.h6_scaled,
      basinWidth / 2,
      this.h6_scaled / 2,
      0
    );

    // --- Particle system for spray ---
    const particleGeom = new THREE.BufferGeometry();
    const positions = new Float32Array(this.particleCount * 3);
    // velocities as a Float32Array (vx, vy, vz) for each particle
    this.particleVelocities = new Float32Array(this.particleCount * 3);
    const lifetimes = new Float32Array(this.particleCount); // per-particle life timer

    // offsets used only for randomness in spawn
    const offsets = new Float32Array(this.particleCount);

    // Initialize arrays (positions will be set in resetAll)
    for (let i = 0; i < this.particleCount; i++) {
      positions[i * 3 + 0] = this.pipe56.position.x;
      positions[i * 3 + 1] = -this.h6_scaled;
      positions[i * 3 + 2] = this.pipe56.position.z;
      this.particleVelocities[i * 3 + 0] = 0;
      this.particleVelocities[i * 3 + 1] = 0;
      this.particleVelocities[i * 3 + 2] = 0;
      lifetimes[i] = 0.0;
      offsets[i] = Math.random();
    }

    particleGeom.setAttribute(
      "position",
      new THREE.BufferAttribute(positions, 3)
    );
    particleGeom.setAttribute("offset", new THREE.BufferAttribute(offsets, 1));
    particleGeom.setAttribute(
      "lifetime",
      new THREE.BufferAttribute(lifetimes, 1)
    );

    const particleMaterial = new THREE.PointsMaterial({
      color: 0x80c0ff,
      size: 0.12,
      sizeAttenuation: true,
      transparent: true,
      opacity: 0.9,
      depthWrite: false,
      blending: THREE.AdditiveBlending,
    });

    this.fountainParticles = new THREE.Points(particleGeom, particleMaterial);
    this.scene.add(this.fountainParticles);

    // --- Core stream mesh: thin translucent cylinder/cone to emulate continuous flow ---
    const coreRadius = 0.06 * this.scale; // visual radius (scaled)
    const coreHeight = 0.1; // will be scaled dynamically
    const coreGeom = new THREE.CylinderGeometry(
      coreRadius,
      coreRadius,
      coreHeight,
      12,
      1,
      true
    );
    const coreMat = new THREE.MeshPhysicalMaterial({
      color: 0x60b8ff,
      transmission: 0.8,
      transparent: true,
      opacity: 0.5,
      roughness: 0.2,
      metalness: 0.0,
      side: THREE.DoubleSide,
    });
    this.coreStream = new THREE.Mesh(coreGeom, coreMat);
    // initially hide it (scale.y = 0)
    this.coreStream.visible = false;
    this.scene.add(this.coreStream);
  }

  setupGUI() {
    const gui = new GUI({ title: "Heron's Fountain Controls" });
    gui.add(this.guiParams, "playPause").name(" /  Start/Pause");
    gui.add(this.guiParams, "reset").name(" Reset All");
    gui.add(this.guiParams, "timeScale", 0.1, 5.0, 0.1).name("Time Scale");
    gui
      .add(this.guiParams, "viewMode", ["2D", "3D"])
      .name("View Mode")
      .onChange((value) => {
        this.camera = value === "2D" ? this.camera2D : this.camera3D;
        this.controls.enabled = value === "3D";
        this.onWindowResize();
      });
  }

  resetAll() {
    this.guiParams.isRunning = false;
    this.clock.stop();
    this.elapsedTime = 0.0;
    this.physics.reset();
    this.h_jet = 0.0;

    this.updateWaterMeshes();

    // Reset particles to nozzle
    const positions = this.fountainParticles.geometry.getAttribute("position");
    const lifetimes = this.fountainParticles.geometry.getAttribute("lifetime");
    const nozzleX = this.pipe56.position.x;
    const nozzleY = this.h6_scaled;
    const nozzleZ = this.pipe56.position.z;

    for (let i = 0; i < this.particleCount; i++) {
      positions.array[i * 3 + 0] = nozzleX;
      positions.array[i * 3 + 1] = nozzleY;
      positions.array[i * 3 + 2] = nozzleZ;
      this.particleVelocities[i * 3 + 0] = 0;
      this.particleVelocities[i * 3 + 1] = 0;
      this.particleVelocities[i * 3 + 2] = 0;
      lifetimes.array[i] = 0.0;
    }
    positions.needsUpdate = true;
    lifetimes.needsUpdate = true;

    // Hide core stream until velocity > 0
    this.coreStream.visible = false;
    this.coreStream.scale.set(1, 0.001, 1);

    this.updateTextLabels();
  }

  handlePlayPause() {
    if (this.guiParams.isRunning) {
      if (this.physics.isStable) {
        this.resetAll();
      }
      this.guiParams.isRunning = true;
      this.clock.start();
    } else {
      this.clock.stop();
    }
  }

  updateWaterMeshes() {
    const { h_A, h_B, h_C } = this.physics;
    const s = this.scale;

    const hA_s = Math.max(0.01, h_A * s);
    const hB_s = Math.max(0.01, h_B * s);
    const hC_s = Math.max(0.01, h_C * s);

    this.waterA.scale.y = hA_s;
    this.waterA.position.y = this.H_scaled + hA_s / 2;

    this.waterB.scale.y = hB_s;
    this.waterB.position.y = hB_s / 2;

    this.waterC.scale.y = hC_s;
    this.waterC.position.y = hC_s / 2;
  }

  updateFountain(dt) {
    const positions = this.fountainParticles.geometry.getAttribute("position");
    const lifetimes = this.fountainParticles.geometry.getAttribute("lifetime");
    const vels = this.particleVelocities;
    const gravity = new THREE.Vector3(0, -this.physics.g * this.scale * 0.8, 0);
    const v4_scaled = Math.max(0, this.physics.v4) * this.scale;

    // nozzle world position (top of pipe56)
    // pipe56 is centered, with height = this.h6_scaled, positioned at y = this.h6_scaled/2
    // top = position.y + height/2 -> equals this.h6_scaled
    const nozzleX = this.pipe56.position.x;
    const nozzleY = this.h6_scaled;
    const nozzleZ = this.pipe56.position.z;

    // direction of the pipe's axis in world coordinates (cylinder default points +Y)
    const pipeDir = new THREE.Vector3(0, 1, 0)
      .applyQuaternion(this.pipe56.quaternion)
      .normalize();

    // boundaries of basin A (where particles "land")
    const waterATop = this.waterA.position.y + this.waterA.scale.y / 2;
    const basinAMinX = -this.worldWidth / 2;
    const basinAMaxX = this.worldWidth / 2;
    const basinAMinZ = -this.worldDepth / 2;
    const basinAMaxZ = this.worldDepth / 2;

    // tune turbulence and spread
    const TURBULENCE_STRENGTH = 1.2; // lateral randomness while airborne
    const LATERAL_SPREAD_RATIO = 0.04; // lateral speed ratio relative to v4

    const positionsArray = positions.array;
    const lifetimesArray = lifetimes.array;

    for (let i = 0; i < this.particleCount; i++) {
      const idx = i * 3;

      let px = positionsArray[idx];
      let py = positionsArray[idx + 1];
      let pz = positionsArray[idx + 2];

      let vx = vels[idx];
      let vy = vels[idx + 1];
      let vz = vels[idx + 2];

      // Consider particle dead if it's inside basin A water
      const isDead =
        py < waterATop &&
        py > this.H_scaled &&
        px > basinAMinX &&
        px < basinAMaxX &&
        pz > basinAMinZ &&
        pz < basinAMaxZ;

      // Also if lifetime exceeds something or if particles fall too low
      const tooLow = py < -10;

      if (isDead || tooLow) {
        // respawn at nozzle
        px = nozzleX + (Math.random() - 0.5) * 0.01; // tiny jitter so particles don't perfectly overlap
        py = nozzleY;
        pz = nozzleZ + (Math.random() - 0.5) * 0.01;

        lifetimesArray[i] = 0.0;

        if (v4_scaled > 0 && this.guiParams.isRunning) {
          // initial velocity aligned with pipeDir plus slight lateral
          const forwardSpeed = v4_scaled * (0.95 + Math.random() * 0.1); // small jitter in vertical speed
          // lateral random vector orthogonal to pipeDir
          // construct random perpendicular vector:
          let lateral = new THREE.Vector3(
            Math.random() - 0.5,
            Math.random() - 0.5,
            Math.random() - 0.5
          );
          // make it perpendicular to pipeDir:
          lateral
            .sub(pipeDir.clone().multiplyScalar(lateral.dot(pipeDir)))
            .normalize();

          const lateralMag =
            forwardSpeed * LATERAL_SPREAD_RATIO * (0.5 + Math.random() * 1.0);
          const lateralVec = lateral.multiplyScalar(lateralMag);

          vx = pipeDir.x * forwardSpeed + lateralVec.x;
          vy = pipeDir.y * forwardSpeed + lateralVec.y;
          vz = pipeDir.z * forwardSpeed + lateralVec.z;
        } else {
          // no flow
          vx = 0;
          vy = 0;
          vz = 0;
        }
      } else {
        // update particle in flight
        // apply gravity (world space)
        vy += gravity.y * dt;

        // apply turbulence
        vx += (Math.random() * 2 - 1) * TURBULENCE_STRENGTH * dt;
        vz += (Math.random() * 2 - 1) * TURBULENCE_STRENGTH * dt;

        // integrate position
        px += vx * dt;
        py += vy * dt;
        pz += vz * dt;

        lifetimesArray[i] += dt;
      }

      positionsArray[idx] = px;
      positionsArray[idx + 1] = py;
      positionsArray[idx + 2] = pz;

      vels[idx] = vx;
      vels[idx + 1] = vy;
      vels[idx + 2] = vz;
    }

    positions.needsUpdate = true;
    lifetimes.needsUpdate = true;

    // Update continuous core stream based on physics.v4 (ballistic height)
    // h_jet = v^2 / (2g) (in physics units). Convert to scaled units:
    const jetHeightMeters = this.physics.v4 ** 2 / (2 * this.physics.g);
    this.h_jet = jetHeightMeters;
    const jetHeightScaled = jetHeightMeters * this.scale;

    if (this.physics.v4 > 0.01) {
      // show core stream
      this.coreStream.visible = true;
      // make sure core has a minimum height for visibility
      const minVisual = 0.02 * this.scale;
      const visualHeight = Math.max(minVisual, jetHeightScaled);

      // The cylinder geometry is centered on its origin; scale.y affects half-height, so we set scale.y such that final height = visualHeight
      // Our base geometry had coreHeight = 0.1 units; so required scale = visualHeight / 0.1
      const baseGeomHeight = 0.1;
      const scaleY = visualHeight / baseGeomHeight;
      this.coreStream.scale.set(1, scaleY, 1);

      // position core: center it along pipe direction from nozzle by half the visualHeight
      // world position = nozzle + pipeDir * (visualHeight/2)
      const centerPos = new THREE.Vector3(nozzleX, nozzleY, nozzleZ).add(
        pipeDir.clone().multiplyScalar(visualHeight / 2)
      );
      this.coreStream.position.copy(centerPos);

      // align core orientation with pipeDir
      // default cylinder axis is +Y; so compute quaternion that rotates +Y to pipeDir
      const up = new THREE.Vector3(0, 1, 0);
      const quat = new THREE.Quaternion().setFromUnitVectors(
        up,
        pipeDir.clone().normalize()
      );
      this.coreStream.quaternion.copy(quat);
    } else {
      // hide it
      this.coreStream.visible = false;
    }
  }

  // --- UI & helpers ---

  addTextLabels() {
    if (this.textOverlay) this.textOverlay.remove();

    this.textOverlay = document.createElement("div");
    this.textOverlay.className = "text-label-overlay";
    this.container.appendChild(this.textOverlay);

    const createLabel = (id) => {
      const div = document.createElement("div");
      div.className = "label-group";
      div.id = id;
      this.textOverlay.appendChild(div);
      return div;
    };

    this.textGroups.A = createLabel("label-A");
    this.textGroups.B = createLabel("label-B");
    this.textGroups.C = createLabel("label-C");
    this.textGroups.Time = createLabel("label-Time");
    this.textGroups.Jet = createLabel("label-Jet");
  }

  toScreenPosition(vector3, camera, canvas) {
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;
    const vector = vector3.clone().project(camera);
    vector.x = ((vector.x + 1) / 2) * width;
    vector.y = (-(vector.y - 1) / 2) * height;
    return { x: vector.x, y: vector.y };
  }

  updateTextLabels() {
    if (!this.textGroups.A) return;

    const s = this.scale;
    const canvas = this.renderer.domElement;

    const posA = new THREE.Vector3(
      0,
      this.H_scaled + this.topBasinHeight - 1,
      0
    );
    const posB = new THREE.Vector3(-this.worldWidth / 4, this.H_scaled - 1, 0);
    const posC = new THREE.Vector3(this.worldWidth / 4, this.H_scaled - 1, 0);
    const posJet = new THREE.Vector3(
      this.worldWidth / 4,
      this.h6_scaled + 2,
      0
    );

    const screenPosA = this.toScreenPosition(posA, this.camera, canvas);
    this.textGroups.A.style.left = `${screenPosA.x}px`;
    this.textGroups.A.style.top = `${screenPosA.y}px`;
    this.textGroups.A.innerHTML = `
      <div>Bacino A</div>
      <div>h_A: ${(this.physics.h_A * 100).toFixed(1)} cm</div>
    `;

    const screenPosB = this.toScreenPosition(posB, this.camera, canvas);
    this.textGroups.B.style.left = `${screenPosB.x}px`;
    this.textGroups.B.style.top = `${screenPosB.y}px`;
    this.textGroups.B.innerHTML = `
      <div>Bacino B</div>
      <div>h_B: ${(this.physics.h_B * 100).toFixed(1)} cm</div>
    `;

    const screenPosC = this.toScreenPosition(posC, this.camera, canvas);
    this.textGroups.C.style.left = `${screenPosC.x}px`;
    this.textGroups.C.style.top = `${screenPosC.y}px`;
    this.textGroups.C.innerHTML = `
      <div>Bacino C</div>
      <div>h_C: ${(this.physics.h_C * 100).toFixed(1)} cm</div>
    `;

    const screenPosJet = this.toScreenPosition(posJet, this.camera, canvas);
    this.textGroups.Jet.style.left = `${screenPosJet.x}px`;
    this.textGroups.Jet.style.top = `${screenPosJet.y}px`;
    this.textGroups.Jet.innerHTML = `
      <div>Getto</div>
      <div>v_4: ${this.physics.v4.toFixed(2)} m/s</div>
      <div>h_jet: ${(this.h_jet * 100).toFixed(1)} cm</div>
    `;

    this.textGroups.Time.style.left = `50%`;
    this.textGroups.Time.style.top = `20px`;
    const status = this.physics.isStable
      ? "STABILE"
      : this.guiParams.isRunning
      ? "RUNNING"
      : "PAUSED";
    this.textGroups.Time.innerHTML = `
      <div>Tempo: ${this.physics.t.toFixed(2)} s</div>
      <div>Stato: ${status}</div>
    `;
  }

  onWindowResize() {
    const aspect = this.container.clientWidth / this.container.clientHeight;
    const viewSize = 15;

    this.camera2D.left = -viewSize * aspect;
    this.camera2D.right = viewSize * aspect;
    this.camera2D.top = viewSize;
    this.camera2D.bottom = -viewSize;
    this.camera2D.updateProjectionMatrix();

    this.camera3D.aspect = aspect;
    this.camera3D.updateProjectionMatrix();

    this.renderer.setSize(
      this.container.clientWidth,
      this.container.clientHeight
    );
  }

  animate() {
    requestAnimationFrame(this.animate);

    if (this.guiParams.viewMode === "3D") {
      this.controls.update();
    }

    if (this.guiParams.isRunning) {
      const delta = this.clock.getDelta() * this.guiParams.timeScale;
      this.elapsedTime += delta;

      if (!this.physics.isStable) {
        this.physics.step(delta);
        this.updateWaterMeshes();
      } else {
        this.guiParams.isRunning = false;
        this.clock.stop();
      }

      this.updateFountain(delta);
    } else {
      // even paused, keep small updates for visual core (so core hides/shows with v4)
      // but do not advance physics
      this.updateFountain(0.0);
    }

    this.updateTextLabels();
    this.renderer.render(this.scene, this.camera);
  }
}

// Initialize when DOM ready
document.addEventListener("DOMContentLoaded", () => {
  try {
    new HeronFountainScene("scene-container");
  } catch (e) {
    console.error("Impossibile inizializzare la scena:", e);
    document.getElementById("scene-container").innerHTML =
      "<h3>Errore durante l'inizializzazione della simulazione.</h3><pre>" +
      e.stack +
      "</pre>";
  }
});
